import { Inline } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import Mention from '../models';

export default {
  name: 'mention',

  match(node: JsonML): boolean {
    return (
      getTagName(node) === 'span' &&
      getAttributes(node)['data-type'] === 'mention'
    );
  },

  convert(node: JsonML): Inline {
    const data = { ...getAttributes(node) };
    delete data['data-type'];
    return Mention.create({
      data,
    });
  },
};
